*{
  font-family: Helvetica, Arial, sans-serif;
}

body{
  padding: 60px;
}

input{
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
  
}

.card p{
  padding: 5px;
  text-align: center;
  font-weight: bold;
}

.card img{
  max-height: 170px;
}

h3{
  text-align: center;
}

h2{
  text-align: center;
  font-weight:bold;
  font-size: 40px;
}

